import React from 'react';
import styles from "./index.module.less";
import {Button, Form, Input} from "antd";
import {useSendMailMutation} from "../../api/api";

function Email(props) {
    const [form] = Form.useForm();
    const [sendEmail] = useSendMailMutation()
    const onFinish = (value) => {
        const {address} = value
        sendEmail({
            to: address,
            subject: "Welcome",
            content: "Welcome to DEV@Deakin"
        })
    }
    return (
        <Form form={form} name="email" onFinish={onFinish} className={styles.header}>
            <label>SIGN UP FOR OUR DAILY INSIDER</label>
            <Form.Item name="address" rules={[{type: 'email'}]}>
                <Input className placeholder="Enter your email"/>
            </Form.Item>
            <Form.Item>
                <Button type="primary" htmlType="submit" size="small">
                    Subscribe
                </Button>
            </Form.Item>

        </Form>
    );
}

export default Email;